import React, { useState, useEffect } from 'react';
import 'ol/ol.css';
import Map from 'ol/Map';
import View from 'ol/View';
import TileLayer from 'ol/layer/Tile';
import OSM from 'ol/source/OSM';
import VectorLayer from 'ol/layer/Vector';
import VectorSource from 'ol/source/Vector';
import GeoJSON from 'ol/format/GeoJSON';

const MapComponent = () => {
  const [map, setMap] = useState(null);

  useEffect(() => {
    const initMap = new Map({
      target: 'map',
      layers: [
        new TileLayer({
          source: new OSM({
            wrapX: true, // Ensure OSM tiles wrap the antimeridian
          }),
        }),
      ],
      view: new View({
        center: [0, 0],
        zoom: 2,
      }),
    });

    setMap(initMap);
  }, []);

  useEffect(() => {
    if (map) {
      fetch('http://localhost:8000/api/positions/')
        .then((response) => response.json())
        .then((data) => {
          const vectorSource = new VectorSource({
            features: new GeoJSON().readFeatures(data, {
              featureProjection: 'EPSG:3857', // Match the projection used in the map
            }),
            wrapX: true, // Ensure vector source wraps the antimeridian
          });

          const vectorLayer = new VectorLayer({
            source: vectorSource,
          });

          map.addLayer(vectorLayer);
        });
    }
  }, [map]);

  return <div id="map" style={{ height: '400px', width: '100%' }} />;
};

export default MapComponent;
