import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import MapComponent from '../components/maps/CruiseListMap';
import CountryFlag from 'react-country-flag';
import Tooltip from '../components/Tooltip';
import './CruiseListPage.css';
import countries from 'i18n-iso-countries';

countries.registerLocale(require('i18n-iso-countries/langs/en.json'));

const CruiseListPage = () => {
    const [activeTab, setActiveTab] = useState('list');
    const [currentPage, setCurrentPage] = useState(1);
    const [itemsPerPage] = useState(10);
    const [searchTerm, setSearchTerm] = useState('');
    const [filters, setFilters] = useState({ status: '' });
    const [cruises, setCruises] = useState([]);
    const [statuses, setStatuses] = useState([]);
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState(null);
    const navigate = useNavigate();

    useEffect(() => {
        const fetchCruises = async () => {
            try {
                const response = await fetch('http://localhost:8000/api/cruises/', {
                    headers: {
                        'Accept': 'application/json',
                    }
                });
                if (!response.ok) {
                    throw new Error(`HTTP error! status: ${response.status}`);
                }
                const data = await response.json();
                setCruises(data);
            } catch (error) {
                setError('Failed to load cruises: ' + error.message);
            } finally {
                setLoading(false);
            }
        };
        fetchCruises();
    }, []);
    
    useEffect(() => {
        const fetchStatuses = async () => {
            try {
                const response = await fetch('http://localhost:8000/api/statuses/', {
                    headers: {
                        'Accept': 'application/json',
                    }
                });
                if (!response.ok) {
                    throw new Error(`HTTP error! status: ${response.status}`);
                }
                const data = await response.json();
                setStatuses(data);
            } catch (error) {
                setError('Failed to load statuses: ' + error.message);
            }
        };
        fetchStatuses();
    }, []);
    
    
    
    const handleSearchChange = (e) => {
        setSearchTerm(e.target.value);
    };

    const getCountryName = (isoCode) => {
        return countries.getName(isoCode, "en", { select: "official" }) || 'No country';
    };

    const filteredCruises = cruises.filter(cruise => {
        const name = cruise.cruise_name.toLowerCase();
        const status = cruise.status_name;
        return name.includes(searchTerm.toLowerCase()) &&
            (!filters.status || status === filters.status);
    });

    const indexOfLastCruise = currentPage * itemsPerPage;
    const indexOfFirstCruise = indexOfLastCruise - itemsPerPage;
    const currentCruises = filteredCruises.slice(indexOfFirstCruise, indexOfLastCruise);

    const handlePageChange = pageNumber => setCurrentPage(pageNumber);
    const handleSeeDetails = id => navigate(`/cruises/${id}`);

    return (
        <div className="cruise-list-container">
            <header>
                <h1>Scientific Cruises - SPC</h1>
                <nav>
                    <button onClick={() => setActiveTab('list')} className={activeTab === 'list' ? 'tab active' : 'tab'}>
                        List
                    </button>
                    <button onClick={() => setActiveTab('map')} className={activeTab === 'map' ? 'tab active' : 'tab'}>
                        Map
                    </button>
                </nav>
            </header>
            <main>
                {activeTab === 'list' && (
                    <section>
                        <h2>List Cruises</h2>
                        <div className="search-filter-container">
                            <input
                                type="text"
                                placeholder="Search cruises..."
                                onChange={handleSearchChange}
                                className="search-input"
                            />
                            <select
                                value={filters.status}
                                onChange={(e) => setFilters({ ...filters, status: e.target.value })}
                                className="status-filter"
                            >
                                <option value="">All Statuses</option>
                                {statuses.map((status) => (
                                    <option key={status.id} value={status.name}>{status.name}</option>
                                ))}
                            </select>
                        </div>
                        {loading && <div>Loading...</div>}
                        {error && <div>Error: {error}</div>}
                        {!loading && !error && (
                            <div className="cruise-list-table-container">
                                <table className="cruise-list-table">
                                    <thead>
                                        <tr>
                                            <th>Name</th>
                                            <th>Status</th>
                                            <th>Country</th>
                                            <th>Vessel</th>
                                            <th>First Departure</th>
                                            <th>Last Arrival</th>
                                            <th>Actions</th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        {currentCruises.map(cruise => (
                                            <tr key={cruise.cruise_id}>
                                                <td>{cruise.cruise_name}</td>
                                                <td>{cruise.status_name}</td>
                                                <td>
                                                    <Tooltip text={getCountryName(cruise.iso2_country)}>
                                                        <CountryFlag
                                                            countryCode={cruise.iso2_country}
                                                            svg
                                                            style={{ width: '50px', height: '30px' }}
                                                        />
                                                    </Tooltip>
                                                </td>
                                                <td>{cruise.vessel_details ? cruise.vessel_details.vessel_name : 'N/A'}</td>
                                                <td>{cruise.legs && cruise.legs[0] ? cruise.legs[0].start_date : 'N/A'}</td>
                                                <td>{cruise.legs && cruise.legs[cruise.legs.length - 1] ? cruise.legs[cruise.legs.length - 1].end_date : 'N/A'}</td>
                                                <td>
                                                    <button onClick={() => handleSeeDetails(cruise.cruise_id)} className="details-button">
                                                        See Details
                                                    </button>
                                                </td>
                                            </tr>
                                        ))}
                                    </tbody>
                                </table>
                            </div>
                        )}
                        <div className="pagination">
                            {Array.from({ length: Math.ceil(filteredCruises.length / itemsPerPage) }, (_, index) => (
                                <button key={index} onClick={() => handlePageChange(index + 1)} className={currentPage === index + 1 ? 'page-btn active' : 'page-btn'}>
                                    {index + 1}
                                </button>
                            ))}
                        </div>
                    </section>
                )}
                {activeTab === 'map' && <MapComponent />}
            </main>
        </div>
    );
};

export default CruiseListPage;
